<template>
  <table>
   <thead>
      <tr>
        <th @click="sortByName">نام</th>
        <th @click="sortByNumber">شماره</th>
      </tr>
   </thead>
   <tbody>
      <tr v-for="item in items"  :key="item.number">
        <td>{{item.name}}</td>
        <td>{{item.number}}</td>
      </tr>
   </tbody>

  </table>
  
</template>



<script>


export default {
  name: 'App',
  data:()=>({
    items:[{name:'علی',number:1},
           {name:'حسین',number:3},
           {name:'سجاد',number:4},
           {name:'باقر',number:5},
           {name:'رضا',number:8},
           {name:'کاظم',number:7},
           {name:'صادق',number:6},
           {name:'حسن',number:2}]
    ,
    numstatus:false
    ,
    namestatus:false


  }),

  methods:{
    sortByName(){
      if(this.namestatus){
            this.namestatus=false;
            this.items.sort((a,b)=>{
            if(a.name>b.name) return 1;
            if(a.name<b.name) return -1;
            return 0 ;    })
      }else{
            this.namestatus=true;
            this.items.sort((a,b)=>{
            if(a.name>b.name) return -1;
            if(a.name<b.name) return  1;
            return 0 ;    })
      }
    },
    sortByNumber(){
      if(this.numstatus){
            this.numstatus=false;
            this.items.sort((a,b)=>{
            return a.number-b.number; 
            })
    }else{
            this.numstatus=true;
            this.items.sort((a,b)=>{
            return b.number-a.number; 
            })
    }
    } 

  }
}
</script>



